@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/lato/opensans.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/opensans-bold.woff2") format("woff2"), url("../fonts/oswald/opensans-bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

.page {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  font-size: 17px;
  line-height: 24px;
  font-family: "Open Sans", "Arial", sans-serif;
}
@media (min-width: 768px) {
  .page {
    font-size: 20px;
    line-height: 24px;
  }
}

.page__page-main {
  flex-grow: 1;
}

.page__advantages {
  margin-bottom: 58px;
}
@media (min-width: 768px) {
  .page__advantages {
    margin-bottom: 83px;
  }
}

.page__contacts {
  margin-bottom: 39px;
}
@media (min-width: 768px) {
  .page__contacts {
    margin-bottom: 57px;
  }
}

.page__page-footer {
  margin-top: auto;
}

.button {
  display: inline-flex;
  justify-content: center;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  font-family: inherit;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #ffffff;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .button {
    font-size: 17px;
  }
}
.button--neutral {
  padding: 14px 20px;
  color: #444444;
  border: 2px solid #444444;
}
.button--neutral:hover {
  color: #ffffff;
  background-color: #444444;
}
.button--neutral:active {
  color: rgba(255, 255, 255, 0.3);
}
.button--bright {
  color: #ffffff;
  background-color: #63d1bb;
  padding: 18px;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;
  display: none;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.9);
}
.modal--opened {
  display: flex;
}

.modal__content {
  position: relative;
  margin: auto;
  background: #ffffff;
  outline: 1px solid #c6c6c6;
  box-shadow: 0 5px 10px 0 rgba(0, 1, 1, 0.25);
}

.social {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 18px 0 20px 0;
  list-style-type: none;
  gap: 15px;
}
@media (min-width: 768px) {
  .social {
    padding: 0;
  }
}

.social__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 42px;
  height: 42px;
}
.social__link::before {
  content: "";
  display: block;
  width: 28px;
  height: 22px;
  background-color: #231f20;
  mask-position: center;
  mask-repeat: no-repeat;
}

.social__link--vk::before {
  mask-image: url("../img/stack.svg#vkontakte");
}

.social__link--youtube::before {
  mask-image: url("../img/stack.svg#youtube");
}

.social__link--telegram::before {
  mask-image: url("../img/stack.svg#telegram");
}

.social__link:hover::before {
  background-color: #46c1ae;
}

.input__input {
  padding: 16px 18px 18px 18px;
  font-size: 19px;
  line-height: 18px;
  font-family: inherit;
  color: #444444;
  background-color: #ffffff;
  border: none;
  box-shadow: inset 0 -2px 0 0 #000000;
}
.input__input ::placeholder {
  color: rgba(0, 0, 0, 0.3);
}

.textarea {
  font-weight: 400;
  font-size: 17px;
  line-height: 18px;
  color: #000000;
  font-family: inherit;
}
@media (min-width: 768px) {
  .textarea {
    font-size: 19px;
    line-height: 22px;
  }
}
.textarea ::placeholder {
  color: rgba(0, 0, 0, 0.3);
  opacity: 1;
}

.main-header {
  position: relative;
  display: flex;
  justify-content: center;
  min-height: 79px;
}
@media (min-width: 768px) {
  .main-header {
    overflow: hidden;
    background-color: #fafafa;
  }
}

.main-header__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 4;
  display: flex;
  justify-content: center;
  width: 79px;
  height: 79px;
  padding-top: 32px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .main-header__toggle {
    display: none;
  }
}
.main-header__toggle:hover {
  opacity: 0.6;
}
.main-header__toggle:active {
  opacity: 0.8;
}
.main-header__toggle::before {
  content: "";
  width: 20px;
  height: 2px;
  background-color: #231f20;
  box-shadow: 0 6px 0 #231f20, 0 12px 0 #231f20;
}
.is-open .main-header__toggle::before, .is-open .main-header__toggle::after {
  content: "";
  position: absolute;
  top: 38px;
  left: 25px;
  width: 26px;
  height: 2px;
  background-color: #000000;
}
.is-open .main-header__toggle::before {
  box-shadow: none;
  transform: rotate(44deg);
}
.is-open .main-header__toggle::after {
  transform: rotate(-44deg);
}
.no-js .main-header__toggle {
  display: none;
}

.main-header__wrapper {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 3;
  background-color: #ffffff;
  box-shadow: inset 0 -1px 0 0 #ececec;
}
@media (min-width: 768px) {
  .main-header__wrapper {
    position: relative;
    display: grid;
    width: 100%;
    max-width: 1150px;
    margin: 0 auto;
    padding: 0 50px;
    background-color: transparent;
    box-shadow: none;
    grid-template-columns: 46% 1fr;
  }
}
@media (min-width: 1150px) {
  .main-header__wrapper {
    display: grid;
    grid-template-columns: 1fr 149px 1fr;
    grid-template-areas: "nav             logo  user-nav" "additional-nav  .     text";
  }
}
.no-js .main-header__wrapper {
  position: relative;
  width: 100%;
}

.main-header__nav {
  display: flex;
  flex-direction: column;
  padding-top: 22px;
  justify-items: center;
}
@media (min-width: 1150px) {
  .main-header__nav {
    display: contents;
  }
}

.main-header__logo {
  align-self: start;
  margin: 0 auto 17px;
}
@media (min-width: 768px) {
  .main-header__logo {
    margin: 0 auto 17px 0;
  }
}
@media (min-width: 1150px) {
  .main-header__logo {
    grid-area: logo;
    margin: 9px 0 0 0;
  }
}
.main-header__logo:hover {
  opacity: 0.6;
}
.main-header__logo:active {
  opacity: 0.3;
}

.main-header__nav-list {
  display: none;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (min-width: 768px) {
  .main-header__nav-list {
    display: flex;
    flex-wrap: wrap;
    gap: 0 37px;
  }
}
@media (min-width: 1150px) {
  .main-header__nav-list {
    grid-area: nav;
    order: -1;
    width: 430px;
    display: grid;
    grid-template-columns: 43% 1fr;
  }
}
.is-open .main-header__nav-list, .no-js .main-header__nav-list {
  display: block;
}
@media (min-width: 768px) {
  .is-open .main-header__nav-list, .no-js .main-header__nav-list {
    display: flex;
  }
}

@media (min-width: 1150px) {
  .main-header__nav-item {
    padding-right: 20px;
  }
}
@media (min-width: 1150px) {
  .main-header__nav-item:nth-child(odd) {
    border-right: 1px solid rgba(0, 0, 0, 0.1);
  }
}

.main-header__nav-link {
  position: relative;
  display: block;
  padding: 28px 30px 27px 76px;
  font-weight: 700;
  color: #000000;
  text-decoration: none;
  box-shadow: inset 0 1px 0 0 #ececec;
}
@media (min-width: 768px) {
  .main-header__nav-link {
    padding: 20px 0;
    font-size: 15px;
    box-shadow: none;
  }
}
@media (min-width: 1150px) {
  .main-header__nav-link {
    padding: 32px 0;
    font-size: 17px;
  }
}
.main-header__nav-link:hover {
  color: rgba(0, 0, 0, 0.6);
}
.main-header__nav-link:active {
  color: rgba(0, 0, 0, 0.3);
}
.main-header__nav-link:focus-visible {
  z-index: 1;
}

.main-header__additional-nav-list {
  display: none;
}
@media (min-width: 1150px) {
  .main-header__additional-nav-list {
    grid-area: additional-nav;
    display: grid;
    grid-template-columns: 43% 1fr;
    width: 430px;
    margin: 0;
    padding: 2px 0;
    list-style-type: none;
    gap: 0 37px;
  }
}

@media (min-width: 1150px) {
  .main-header__additional-nav-item {
    padding-right: 20px;
  }
}

.main-header__additional-nav-link {
  font-weight: 700;
  font-size: 15px;
  color: #000000;
  text-decoration: none;
}
.main-header__additional-nav-link:hover {
  color: rgba(0, 0, 0, 0.6);
}
.main-header__additional-nav-link:active {
  color: rgba(0, 0, 0, 0.3);
}
@media (min-width: 1150px) {
  .main-header__additional-nav-link {
    display: block;
    padding: 18px 0;
  }
}

.main-header__user-nav-wrapper {
  display: none;
}
@media (min-width: 768px) {
  .main-header__user-nav-wrapper {
    display: grid;
    align-content: start;
    gap: 20px;
  }
}
@media (min-width: 1150px) {
  .main-header__user-nav-wrapper {
    display: contents;
  }
}
.is-open .main-header__user-nav-wrapper, .no-js .main-header__user-nav-wrapper {
  display: block;
}
@media (min-width: 768px) {
  .is-open .main-header__user-nav-wrapper, .no-js .main-header__user-nav-wrapper {
    display: grid;
  }
}

@media (min-width: 768px) {
  .main-header__user-nav {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: end;
  }
}
@media (min-width: 1150px) {
  .main-header__user-nav {
    grid-area: user-nav;
  }
}
@media (min-width: 768px) {
  .main-header__user-nav::before {
    content: "";
    position: absolute;
    top: 0;
    left: 50%;
    z-index: -1;
    width: 200vw;
    height: 100%;
    background-color: #ffffff;
    box-shadow: 0 1px 0 0 rgba(0, 0, 0, 0.1);
    transform: translateX(-50%);
  }
}

.main-header__user-nav-link {
  position: relative;
  display: block;
  padding: 28px 30px 27px 76px;
  font-weight: 700;
  color: #000000;
  text-decoration: none;
  box-shadow: inset 0 1px 0 0 #ececec;
}
@media (min-width: 768px) {
  .main-header__user-nav-link {
    max-width: 265px;
    min-height: 88px;
    padding: 32px 0 32px 83px;
    font-size: 17px;
    line-height: 24px;
    text-align: right;
  }
}
@media (min-width: 1150px) {
  .main-header__user-nav-link {
    max-width: 266px;
  }
}
.main-header__user-nav-link:hover {
  color: rgba(0, 0, 0, 0.6);
}
.main-header__user-nav-link:active {
  color: rgba(0, 0, 0, 0.3);
}
.main-header__user-nav-link:focus-visible {
  z-index: 1;
}
@media (min-width: 768px) {
  .main-header__user-nav-link--search {
    align-self: start;
    box-shadow: inset 1px 0 0 0 #e5e5e5, inset -1px 0 0 0 #e5e5e5;
  }
}
.main-header__user-nav-link--search::before {
  mask-image: url("../img/stack.svg#search");
}
@media (min-width: 768px) {
  .main-header__user-nav-link--cart {
    box-shadow: none;
  }
}
.main-header__user-nav-link--cart::before {
  mask-image: url("../img/stack.svg#cart");
}
.main-header__user-nav-link::before {
  content: "";
  position: absolute;
  top: 32px;
  left: 30px;
  width: 21px;
  height: 18px;
  background-color: rgba(0, 0, 0, 0.2);
  mask-position: left;
  mask-repeat: no-repeat;
}
@media (min-width: 768px) {
  .main-header__user-nav-link::before {
    top: 36px;
    left: 31px;
  }
}

.main-header__user-nav-link:hover::before {
  background-color: #63d1bb;
}

.main-header__user-nav-link:active::before {
  opacity: 0.3;
}

@media (min-width: 768px) {
  .main-header__user-nav-link-text {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;
    border: 0;
    clip: rect(0 0 0 0);
  }
}

.main-header__text {
  display: none;
}
@media (min-width: 768px) {
  .main-header__text {
    display: block;
    font-size: 15px;
    text-align: right;
    color: #444444;
  }
}
@media (min-width: 1150px) {
  .main-header__text {
    grid-area: text;
    margin-top: 21px;
  }
}

.hero {
  position: relative;
  padding-top: 73px;
  overflow: hidden;
  background-color: #f1f1f1;
}
@media (min-width: 768px) {
  .hero {
    padding-top: 91px;
  }
}
@media (min-width: 1150px) {
  .hero {
    padding-top: 94px;
  }
}
.hero--index {
  display: flex;
  flex-direction: column;
  min-height: 489px;
  background-color: #fafafa;
}
@media (min-width: 768px) {
  .hero--index {
    min-height: 681px;
  }
}
@media (min-width: 768px) {
  .hero::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: -12px;
    left: 0;
    z-index: 1;
    height: 24px;
    background-image: url("../img/stack.svg#zigzag-fill");
    background-repeat: repeat-x;
    background-size: 770px 24px;
  }
}
@media (min-width: 1150px) {
  .hero::after {
    bottom: -18px;
    height: 34px;
    background-image: url("../img/stack.svg#zigzag-fill-desktop");
    background-position: center;
    background-size: 1250px 34px;
  }
}

.hero__title {
  z-index: 2;
  max-width: 260px;
  margin: 0 auto;
  font-weight: 700;
  font-size: 27px;
  line-height: 30px;
  text-align: center;
}
@media (min-width: 768px) {
  .hero__title {
    max-width: 668px;
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .hero__title {
    font-size: 53px;
    line-height: 57px;
  }
}

.hero__img {
  position: absolute;
  top: -17px;
  left: 50%;
  z-index: 1;
  transform: translateX(-50%);
}
@media (min-width: 768px) {
  .hero__img {
    top: -65px;
  }
}

.hero__categories-list {
  z-index: 1;
  margin: auto 0 0 0;
  padding: 0;
  list-style-type: none;
  background-image: linear-gradient(var(--interior-color) 50%, var(--toys-color) 50%);
  background-clip: content-box;
  --interior-color: #59d5bc;
  --toys-color: #46c1ae;
}
@media (min-width: 768px) {
  .hero__categories-list {
    display: grid;
    width: 100%;
    background-image: linear-gradient(to right, var(--interior-color) 50%, var(--toys-color) 50%);
    grid-template-columns: 1fr 1fr;
    max-width: 1150px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .hero__categories-list {
    padding-left: 50px;
    padding-right: 50px;
  }
}

.hero__categories-link {
  position: relative;
  display: block;
  min-height: 103px;
  padding: 24px 160px 31px 30px;
  font-weight: 700;
  font-size: 19px;
  color: #ffffff;
  text-decoration: none;
  transition: transform 0.3s;
}
@media (min-width: 768px) {
  .hero__categories-link {
    min-height: 119px;
    padding: 25px 160px 34px 36px;
    font-size: 20px;
    line-height: 30px;
  }
}
.hero__categories-link:hover {
  transform: translateY(-5px);
}
.hero__categories-link--interior {
  background-color: #63d1bb;
}
.hero__categories-link--interior:hover {
  background-color: var(--interior-color);
}
.hero__categories-link--toys {
  background-color: #56c3b2;
}
.hero__categories-link--toys:hover {
  background-color: var(--toys-color);
}

.hero__categories-img {
  position: absolute;
  opacity: 0.5;
}

.hero__categories-link--interior .hero__categories-img {
  top: 23px;
  right: 36px;
}
@media (min-width: 768px) {
  .hero__categories-link--interior .hero__categories-img {
    top: 30px;
  }
}

.hero__categories-link--toys .hero__categories-img {
  top: 21px;
  right: 27px;
}
@media (min-width: 768px) {
  .hero__categories-link--toys .hero__categories-img {
    top: 31px;
    right: 33px;
  }
}

.hero__categories-link:hover .hero__categories-img {
  opacity: 0.7;
}

.hero__categories-link:active .hero__categories-img {
  opacity: 0.3;
}

.week-product {
